* {
  box-sizing: border-box;
}

body {
  background-color: rgb(250,250,250);
  color: rgb(40,40,40);
  font-family: 'Poppins', sans-serif;
}

body, h1, h2, h3, p {
  margin: 0;
}

ul {
  padding: 0;
}

a {
  text-decoration: none;
  display: inline-block;
}

.bold {
  font-weight: bold;
}

.gray {
  color: #918E9B;
}

/* navbar */

.navbar {
  height: 70px;
  background-color: #FFFFFF;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.1);
  display: flex;
  padding: 20px 36px;
}

.nav--logo {
  max-width: 50%;
}

/* Hero */

section {
  padding: 20px;
}

.hero {
  display: flex;
  flex-direction: column;
}

.hero--photo {
  max-width: 400px;
  align-self: center;
}

.hero--header {
  margin-top: 32px;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 36px;
  line-height: 40px;
}

.hero--text {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  max-width: 60%;
}

/* card */

.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}

.card {
  width: 176px;
  font-size: 12px;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  line-height: 22px;
  position: relative;
}

.card--image {
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
}

.card--stats {
  display: flex;
  align-items: center;
  gap: 1px;
}

.card--star {
  height: 14px;
}

.card--price {
  margin-top: auto;
}

.card--badge {
  position: absolute;
  background: #FFFFFF;
  border-radius: 2px;
  left: 6px;
  top: 6px;
  font-size: 10.24px;
  height: 20px;
  width: 61px;
  text-align: center;
}


@media only screen and (max-width: 600px) {
  .navbar {
    height: 60px;
  }
  .hero--photo {
    max-width: 300px;
  }
  .hero--header {
    font-size: 26px;
  }
  .hero--text {
    font-size: 10px;
    line-height: 15px;
  }
}

@media only screen and (min-width: 768px) {
  .navbar {
    height: 80px;
  }
  .hero--photo {
  max-width: 600px;
  }
}

@media only screen and (min-width: 992px) {
  .navbar {
    height: 90px;
  }
  .hero--photo {
  max-width: 900px;
  }
}

@media only screen and (min-width: 1200px) {
  .navbar {
    height: 100px;
  }
  .hero--photo{
    max-width: max-content;
  }
  section {
    padding: 80px;
  }
  .hero--header {
    font-size: 90px;
    margin-top: 92px;
    margin-bottom: 76px;
    line-height: 90px;
  }
  .hero--text{
    font-size: 54px;
    line-height: 70px;
  }
}
